<template>
  <div id="app">
    <Header :numCorrect="numCorrect" :numTotal="numTotal" :questionTimer="questionTimer" />
    <b-container class="bv-example-row question-row">
      <b-row v-if="isLoading">
        <b-col sm="6" offset-md="3 ">
          <b-jumbotron>
            <vue-content-loading :primary="primary" :width="400" :height="270">
              <rect x="15" y="13" rx="0" ry="14" width="400" height="50" />
              <rect x="15" y="70" rx="0" ry="10" width="480" height="2" />
              <rect x="15" y="79" rx="0" ry="10" width="400" height="40" />
              <rect x="15" y="125" rx="0" ry="10" width="400" height="40" />
              <rect x="15" y="170" rx="0" ry="10" width="400" height="40" />
              <rect x="15" y="215" rx="0" ry="10" width="400" height="40" />
          
            </vue-content-loading>
          </b-jumbotron>
        </b-col>
      </b-row>

      <b-row v-if="!hasquizfinished">
        <b-col sm="6" offset-md="3 ">
          <Question
            v-if="questions.length"
            :currentQuestion="questions[index]"
            :next="next"
            :increment="increment"
          />
        </b-col>
      </b-row>

      <b-row v-if="hasquizfinished " class="result-row">
        <b-col sm="6" offset="3 ">
          <div class="result-container">
            <h2>Result</h2>
            <p>You Scored {{numCorrect}} out of {{numTotal}}</p>
            <b-button @click="restartquiz">Restart</b-button>
          </div>
        </b-col>
      </b-row>
      <div class="lottie-container" v-if="hasquizfinished && numCorrect >7">
        <lottie
          class="lottie"
          :options="defaultOptions"
          :height="250"
          :width="250"
          v-on:animCreated="handleAnimation"
        />
      </div>
    </b-container>
  </div>
</template>

<script>
import Question from "./components/Question.vue";
import Header from "./components/Header.vue";
import Lottie from "vue-lottie";
import * as animationData from "./assets/lotties-file/celebration.json";
import VueContentLoading from "vue-content-loading";

export default {
  name: "App",
  components: {
    Question,
    Header,
    lottie: Lottie,
    VueContentLoading,
  },
  data() {
    return {
      questions: [],
      index: 0,
      numCorrect: 0,
      numTotal: 0,
      hasquizfinished: false,
      error: "",
      defaultOptions: { animationData: animationData.default },
      animationSpeed: 1,
      fullPage: true,
      tempnumCorrect: 0,
      hasquestionTimeExpire: false,
      questionTimer: 0,
      questionTimerId: 0,
      isLoading: true,
      primary: "#9fa6a1",
    };
  },

  watch: {
    questionTimer: function () {
      if (this.questionTimer == 11 && this.index < this.questions.length - 1) {
        this.index++;
        this.numTotal++;
        this.restartTimer();
      }
      if (
        this.index === this.questions.length - 1 &&
        this.questionTimer == 11
      ) {
        clearInterval(this.questionTimerId);
        this.timer = "finished";
        this.hasquizfinished = true;
      }

      let timer = this.questionTimer;
      return (this.questionTimer = timer);
    },
    index: function () {
      return this.index;
    },
  },

  methods: {
    next() {
      console.log(this.questions.length - 1);
      if (this.index === this.questions.length - 1) {
        this.hasquizfinished = true;
        this.pause;
        this.numCorrect = this.tempnumCorrect;
        clearInterval(this.questionTimerId);
        this.questionTimer = "";
      } else {
        this.restartTimer();
        this.index++;
        this.numTotal++;
      }
    },
    increment(isCorrect) {
      if (this.numTotal < this.questions.length) {
        if (isCorrect) {
          this.numCorrect++;
        }
      }
    },
    restartquiz() {
      location.reload();
    },

    handleAnimation: function (anim) {
      this.anim = anim;
    },

    stop: function () {
      this.anim.stop();
    },

    play: function () {
      this.anim.play();
    },

    pause: function () {
      this.anim.pause();
    },

    onSpeedChange: function () {
      this.anim.setSpeed(this.animationSpeed);
    },
    startTimer() {
      this.questionTimerId = setInterval(() => {
        ++this.questionTimer;
      }, 1000);
      this.hasquestionTimeExpire = false;
    },
    restartTimer() {
      this.questionTimer = 0;
      this.hasquestionTimeExpire = true;
      clearInterval(this.questionTimerId);
      this.startTimer();
    },
    clearTimer() {
      clearInterval(this);
    },
  },
  mounted: function () {
    fetch("https://opentdb.com/api.php?amount=10&category=24&type=multiple", {
      method: "get",
    })
      .then((response) => {
        return response.json();
      })
      .then((jsonData) => {
        this.questions = jsonData.results;
        this.numTotal++;
        this.startTimer();
        this.isLoading = false;
      })
      .catch((err) => {
        this.error = err;
      });
  },
};
</script>

